.App {
  text-align: left;
}

body {
  background-color: #b2c7da;
  white-space: pre-wrap;
}

.topHeader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.topPanel {
  display:flex;
  align-items: center;
  background: white;
  width: 100%;
  color: black;
  justify-content: space-between;
  height: 45px;
}

.fileIcon {
  display: flex;
  height:35px;
  padding-left: 20px;
  justify-content: flex-start;
}

.opposite {
  display: flex;
  justify-content: center;
  font-size: 16px;
  font-weight: 400;
}

.menu {
  display: flex;
  padding-right: 20px;
  justify-content: flex-end;
  height: 35px;
}


.topHeart {
  display:flex;
  align-items: center;
  background: white;
  width: 100%;
  color: black;
  justify-content: center;
  align-items:center;
  height: 30px;
  border-end-start-radius: 20px;
  border-end-end-radius: 20px;
  box-shadow: 0px 2px 2px 0px rgb(158, 158, 158);
  font-size: 13px;

}

.bubble {
  height: 10px;
}

.heart {
  height: 13px;
  margin-left: 6px;
  margin-right: 6px;
}

.pseudoHeader {
  width: 100%;
  height: 75px;
}


.bottomHeader {
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: white;
  height: 35px;
  justify-content: space-between;
  align-items: center;
  padding: 3px 20px;
}

.inputSpan {
  display: flex;
  background-color: #f0f0f0;
  height: 30px;
  width: 100vw;
  flex-direction: row-reverse;
  align-items: center;
  border-radius: 25px;
}

.emoticon {
  height: 20px;
  margin-right: 20px;
}

.send {
  display: flex;
  height: 20px;
  margin-right:40px;
}


.chat-box {
  display: flex;
  width: auto;
  height: auto;
  margin-top: 20px;
}

.chat-box-profile-picture {
  display: flex;
  width: 50px;
  height: 50px;
}

.profile {
  display: flex;
  height: 50px;
}

.chat-box-main {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: auto;
  margin-left: 5px;
}

.chat-box-header {
  padding-top: 0px;
  margin: 0px;
}

.profileName {
  display: flex;
  font-weight: 700;
  font-size: 12px;
  align-items: center;
}

.celeb {
  height: 13px;
  margin-right: 5px;
}

.chat-box-body {
  display: inline-block;
  position: relative;
  background-color: white;
  border-top-right-radius: 15px;
  border-end-start-radius: 15px;
  border-end-end-radius: 15px;
  color: black;
  padding: 6px 10px;
  margin-top: 5px;
  min-width: 40px;
  max-width: 65vw;
  word-wrap: break-word;
  word-break: keep-all;
  font-weight: 400;
  font-size: 14px;
  ;
}

.text {
  margin-block: 8px;
}


.chat-box-date {
  display: flex;
  align-items: flex-end;
  padding-left: 5px;
  font-size: 10px;
  font-weight: 600;
  color: #6c7d89;
  margin-block-end: 0px;
}

.date {
  margin-block-end: 0px;
}


.chat-box-me {
  display: flex;
  flex-direction: row-reverse;
  width: auto;
  height: auto;
  margin-top: 20px;
}

.chat-box-main-me {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: auto;
  margin-right: 7px;
}

.chat-box-body-me {
  display: inline-block;
  position: relative;
  background-color: #646464;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-end-start-radius: 15px;
  color: white;
  padding: 6px 10px;
  margin-top: 5px;
  min-width: 40px;
  max-width: 65vw;
  word-wrap: break-word;
  word-break: keep-all;
  font-weight: 400;
  font-size: 14px;
}

.chat-box-date-me {
  display: flex;
  align-items: flex-end;
  padding-right: 5px;
  text-align: right;
  font-size: 10px;
  font-weight: 600;
  color: #6c7d89;
  margin-block-end: 0px;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
